import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import Card from '@material-ui/core/Card/Card';
import TextField from '@material-ui/core/TextField';
import Tooltip from '@material-ui/core/Tooltip/Tooltip';
import Typography from '@material-ui/core/Typography/Typography';
import Button from '@material-ui/core/Button/Button';
import Dialog from '@material-ui/core/Dialog/Dialog';
import AppBar from '@material-ui/core/AppBar/AppBar';
import Toolbar from '@material-ui/core/Toolbar/Toolbar';
import IconButton from '@material-ui/core/IconButton/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import PermIdentity from '@material-ui/icons/PermIdentity';
import InputLabel from '@material-ui/core/InputLabel';
import Input from '@material-ui/core/Input';
import InputAdornment from '@material-ui/core/InputAdornment';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import FormControl from '@material-ui/core/FormControl';

import AES from 'crypto-js/aes';


import { MuiThemeProvider } from '@material-ui/core/styles';
import { withStyles } from '@material-ui/core/styles';

import { localStorage } from '../utils/localStorage';
import { backgroundPage } from '../utils/backgroundPage';

import { hideCreateAccount, setMixinPin } from '../actions/wallet';
import { showSnackbar } from '../actions/snackbar';
import { setAccountInfo, setAssets } from '../actions/account';
import { SCREEN_MY_BET, SCREEN_MY_WALLET, setScreen } from '../actions/app';
import { showWalletBackup } from '../actions/wallet';

import Transition from './Transition';
import { createUser, createUserProxy, exportPrivateKey, updatePin } from '../utils/mixin';
import { initWalletStorage } from '../utils/wallet';


const styles = {
  cssLabel: {
    color: '#fff',
    '&$cssFocused': {
      color: '#fff',
    },
  },
  cssFocused: {},
  cssUnderline: {
    color: '#fff',
    '&:after': {
      borderBottomColor: '#fff',
    },
  },
  cssOutlinedInput: {
    color: '#fff',
    borderColor: '#fff',
    '&$cssFocused $notchedOutline': {
      borderColor: '#fff',
    textAlign: 'center',
    },
  },
  notchedOutline: {
    borderColor: '#fff',
    color: '#fff',
    textAlign: 'center',
  },
};

class CreateAccount extends Component {
  constructor(props) {
    super(props);
    this.state = {
      accountPin: '',
      isLoading: false,
    };
  }

  disableSubmit = () => {

    return this.state.isLoading;
    const { accountPin } = this.state;
    return !accountPin || accountPin.length < 6;
  };

  handleSubmit = event => {
    event.preventDefault();
    this.createAccount();
  };

  handlePinChange = event => {
    let value = event.target.value;
    value = value.replace(/\D/g, ''); // numeric only
    if(value.length>6)
    {
      value = value.substring(0, 6)
    }
    this.setState({ [event.target.name]: value });
  };

  handleClickShowPassword = () => {
    this.setState(state => ({ showPassword: !state.showPassword }));
  };

  createAccount = async () => {
    this.setState({ isLoading: true });
    const {
      setAccountInfo,
      hideCreateAccount,
      showSnackbar,
      setScreen,
      showWalletBackup,
      setMixinPin,
    } = this.props;

    const { accountPin } = this.state;

    try {
      const proxyResult = await createUserProxy();

      this.setState({ isLoading: false });

      const proxyData = JSON.parse(
        Buffer.from(proxyResult.trim(), 'base64').toString()
      );

      const userData = {
        privateKey: proxyData.key,
        clientId: proxyData.uid,
        sessionId: proxyData.sid,
        pinToken: proxyData.pintoken,
      };
      await updatePin(userData, '', accountPin);
      setMixinPin(accountPin);

      await initWalletStorage();

      const clientId = userData.clientId;
      const passwordHashInBackground = await backgroundPage.getPasswordHash();
      const accounts = await localStorage.getAccounts();
      const encryptedUserData = AES.encrypt(
        JSON.stringify(userData),
        passwordHashInBackground
      ).toString();

      const accountExisted =
        accounts.filter(account => account.clientId === clientId).length > 0;
      if (accountExisted) {
        showSnackbar('Account already exists! Please import another one.');
        return;
      }

      const activeAccount = {
        clientId,
        encryptedUserData,
      };

      accounts.push(activeAccount);
      await localStorage.setAccounts(accounts);

      await backgroundPage.setActiveAccount(activeAccount);
      setAccountInfo(accounts, activeAccount);
      showSnackbar('Account is created successfully!');

      const privateKey = exportPrivateKey(userData);
      showWalletBackup(privateKey);
      setScreen(SCREEN_MY_BET);
      hideCreateAccount();
    } catch (e) {
      console.error(e);
      showSnackbar('Failed to create user, please try again latter!');
    }
  };

  render() {
    const { hideCreateAccount, open, classes } = this.props;
    const { accountPin, showPassword } = this.state;
    return (
      <Dialog
        fullScreen
        aria-labelledby="switch-network-title"
        open={open}
        onClose={hideCreateAccount}
        TransitionComponent={Transition}
      >
        <AppBar position="static" className="appBar">
          <Toolbar>
            <Tooltip title="Close">
              <IconButton
                color="inherit"
                onClick={hideCreateAccount}
                aria-label="Close"
              >
                <CloseIcon />
              </IconButton>
            </Tooltip>
            <Typography variant="h6" color="inherit">
              Create Account
            </Typography>
          </Toolbar>
        </AppBar>
        <div className="dialog">
        <div className="card">
          <div className="card register-card">
            <Typography variant="h6">
            <span className="white glowing-pink">
            Create New Account on LuckyBet</span>
            </Typography>
            <p className="white">
              Please remember your PIN as we do not store this information.
            </p>
            <p className="white">
              We gives away <span className="glowing-green">100 CNB</span> to new registered user to start betting now.
            </p>
            <FormControl className="account-pin" margin="normal">
              <TextField
                required
                name="accountPin"
                type={showPassword ? 'text' : 'password'}
                value={accountPin}
                onChange={this.handlePinChange}
                placeholder="6 digit PIN"
                InputLabelProps={{
                      classes: {
                        root: classes.cssLabel,
                      },
                    }}
                InputProps={{
                      width:20,
                      maxLength: 6,
                      classes: {
                        root: classes.cssOutlinedInput,
                        notchedOutline: classes.notchedOutline,
                      },
                      //className: classes.textField,
                    }}
                variant="outlined"
              />
            </FormControl>
            <div className="space" />
            <Button
              size="large"
              className="sign-in-button button"
              color="secondary"
              variant="contained"
              disabled={this.disableSubmit()}
              onClick={this.handleSubmit}
            >
              Create <PermIdentity className="account-details-button-icon" />
            </Button>
            <div className="space" />
          </div>
        </div>
        </div>
      </Dialog>
    );
  }
}

const mapStateToProps = state => ({
  open: state.wallet.createAccountOpen,
});

const mapDispatchToProps = {
  hideCreateAccount,
  setAccountInfo,
  showSnackbar,
  setScreen,
  showWalletBackup,
  setAssets,
  setMixinPin,
};


CreateAccount.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(CreateAccount));
