import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import Dialog from '@material-ui/core/Dialog/Dialog';
import AppBar from '@material-ui/core/AppBar/AppBar';
import Toolbar from '@material-ui/core/Toolbar/Toolbar';
import Tooltip from '@material-ui/core/Tooltip/Tooltip';
import IconButton from '@material-ui/core/IconButton/IconButton';
import Typography from '@material-ui/core/Typography/Typography';
import Card from '@material-ui/core/Card/Card';
import CloseIcon from '@material-ui/icons/Close';
import InputLabel from '@material-ui/core/InputLabel';
import TextField from '@material-ui/core/TextField/TextField';
import InputAdornment from '@material-ui/core/InputAdornment';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import Button from '@material-ui/core/Button';
import FormControl from '@material-ui/core/FormControl';
import FileCopyIcon from '@material-ui/icons/FileCopy';

import { CopyToClipboard } from 'react-copy-to-clipboard';

import { getActiveUserData } from '../utils/crypto';

import Transition from './Transition';

import { hideBackupKeystore } from '../actions/wallet';
import { showSnackbar } from '../actions/snackbar';
import { exportPrivateKey, verifyPin } from '../utils/mixin';

import { MuiThemeProvider } from '@material-ui/core/styles';
import { withStyles } from '@material-ui/core/styles';
import { SNACKBAR_ERROR } from './WalletSnackbar';

const styles = {
  cssLabel: {
    color: '#fff',
    '&$cssFocused': {
      color: '#fff',
    },
  },
  cssFocused: {},
  cssUnderline: {
    color: '#fff',
    '&:after': {
      borderBottomColor: '#fff',
    },
  },
  cssOutlinedInput: {
    color: '#fff',
    borderColor: '#fff',
    '&$cssFocused $notchedOutline': {
      borderColor: '#fff',
    textAlign: 'center',
    },
  },
  notchedOutline: {
    borderColor: '#fff',
    color: '#fff',
    textAlign: 'center',
  },
};

class BackupKeystore extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      backupPin: '',
      privateKey: null,
    };
  }

  handleClickShowPassword = () => {
    this.setState(state => ({ showPassword: !state.showPassword }));
  };

  handlePinChange = event => {
    let value = event.target.value;
    value = value.replace(/\D/g, ''); // numeric only
    this.setState({ [event.target.name]: value });
  };

  disableSubmit = () => {
    return false;
    return !(this.state.backupPin && this.state.backupPin.length === 6);
  };

  handleSubmit = event => {
    event.preventDefault();
    this.showPrivateKey(this.state.backupPin);
  };

  showPrivateKey = async pin => {
    const { activeAccount, showSnackbar } = this.props;
    const userData = await getActiveUserData(activeAccount);
    try {
      await verifyPin(pin, userData);
      const privateKey = exportPrivateKey(userData);
      this.setState({ privateKey });
    } catch (e) {
      console.error(e);
      showSnackbar(`Fail to show private key: ${e.description}`, SNACKBAR_ERROR);
    }
  };

  copyPrivateKey = () => {
    const { showSnackbar } = this.props;
    showSnackbar(`Private key copied to clipboard!`);
  };

  renderPrivateKey() {
    const {classes} = this.props;
    const { privateKey } = this.state;
    if (!privateKey) {
      return null;
    }

    return (
      <div className="card backup-keystore-card">
        <div className="private-key">
          <CopyToClipboard text={privateKey} onCopy={this.copyPrivateKey}>
            <Button
              id="copy-private-key1"
              color="secondary"
              variant="contained"
            >
              Copy private key{' '}
              <FileCopyIcon className="copy-private-key-icon" />
            </Button>
          </CopyToClipboard>
          <TextField
              name="privateKey"
              label="Private key"
              className="private-key-field"
              multiline
              rows="20"
              autoComplete="off"
              value={privateKey}
              InputLabelProps={{
                      classes: {
                        root: classes.cssLabel,
                      },
                    }}
                InputProps={{
                      readOnly: true,
                      width:20,
                      maxLength: 6,
                      classes: {
                        root: classes.cssOutlinedInput,
                        notchedOutline: classes.notchedOutline,
                      },
                      //className: classes.textField,
                    }}
              variant="outlined"
              margin="normal"
            />
            <div className="space" />
        </div>
      </div>
    );
  }

  handleClose = () => {
    const { hideBackupKeystore } = this.props;
    this.setState({ privateKey: null });
    hideBackupKeystore();
  };

  render() {
    const { open, classes } = this.props;
    const { backupPin, showPassword } = this.state;

    return (
      <Dialog
        fullScreen
        open={open}
        onClose={this.handleClose}
        TransitionComponent={Transition}
      >
        <AppBar position="static" className="appBar">
          <Toolbar>
            <Tooltip title="Close">
              <IconButton
                color="inherit"
                onClick={this.handleClose}
                aria-label="Close"
              >
                <CloseIcon />
              </IconButton>
            </Tooltip>
            <Typography variant="h6" color="inherit">
              Backup private key
            </Typography>
          </Toolbar>
        </AppBar>
        <div className="dialog">
        <div className="card">
          <div className="card backup-keystore-card">
            <FormControl className="backup-pin" margin="normal">
              <TextField
                required
                name="backupPin"
                type={showPassword ? 'text' : 'password'}
                value={backupPin}
                onChange={this.handlePinChange}
                InputLabelProps={{
                      classes: {
                        root: classes.cssLabel,
                      },
                    }}
                InputProps={{
                      width:20,
                      maxLength: 6,
                      classes: {
                        root: classes.cssOutlinedInput,
                        notchedOutline: classes.notchedOutline,
                      },
                      //className: classes.textField,
                    }}
                variant="outlined"
                placeholder="Enter your PIN"
              />
            </FormControl>
            <Button
              id="show-private-key-button"
              className="button"
              color="secondary"
              variant="contained"
              disabled={this.disableSubmit()}
              onClick={this.handleSubmit}
            >
              Show backup private key{' '}
              <Visibility className="show-private-key-icon" />
            </Button>
          </div>
          {this.renderPrivateKey()}
        </div>
        </div>
      </Dialog>
    );
  }
}

const mapStateToProps = state => ({
  open: state.wallet.backupKeystoreOpen,
  activeAccount: state.account.activeAccount,
});

const mapDispatchToProps = {
  hideBackupKeystore,
  showSnackbar,
};


BackupKeystore.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(BackupKeystore));
