import React, { Component } from 'react';
import { connect } from 'react-redux';

import Typography from '@material-ui/core/Typography/Typography';
import Button from '@material-ui/core/Button/Button';
import LockOpen from '@material-ui/icons/LockOpen';

import { setMixinPin } from '../actions/wallet';

class SignOut extends Component {
  signOut = () => {
    const { setMixinPin } = this.props;
    setMixinPin(null);
  };

  render() {
    return (
      <div>
        <Typography variant="h6" color="primary" color="inherit">
                  <span className="glowing-pink">Lock Account</span>
                </Typography>
        <div className="space" />
        <Button
          size="large"
          variant="contained"
          color="secondary"
          onClick={this.signOut}
        >
          Lock Account <LockOpen className="account-details-button-icon" />
        </Button>
        <p className="small">Temporarily lock the account, you need to provide the PIN to unlock</p>
      </div>
    );
  }
}

const mapDispatchToProps = {
  setMixinPin,
};

export default connect(
  null,
  mapDispatchToProps
)(SignOut);
